import { computed, defineComponent, ref, watch } from "vue";
import { useStore } from "vuex";
export default defineComponent({
  name: "",
  components: {},
  props: {
    title: {
      type: Array,
      required: true
    },
    isfixed: {
      type: Boolean,
      default: false
    }
  },
  emits: ["tabIndex"],

  setup(props, {
    emit
  }) {
    const store = useStore();
    const mainNav = ref(); // tab标签下标

    const activeIndex = ref(0); // tab点击事件

    const handleClick = (tabIndex, event) => {
      // 记录点击切换的下标
      store.commit("setTabsIndex", tabIndex); // 发送事件，记录切换的下标

      emit('tabIndex', tabIndex);
    }; // 监听tba栏的切换


    watch(() => store.state.tabsIndex, value => {
      activeIndex.value = value;
    });
    return {
      handleClick,
      mainNav,
      activeIndex
    };
  }

});